<template>
<div>
  <el-row>
    
    <el-col :span="8" :offset="3">
      <el-card class="box-card">
        <div slot="header" class="clearfix">
          <span style="float:left;">您可以根据意向选择下列</span>
          <el-button style="float:right;padding:3px 0" type="text">[快捷留言]</el-button>
        </div>
        <div v-for="item in words" :key="item.no" class="text item" @click="changeTextarea(item)">
          {{item.content}}
        </div>
      </el-card>
    </el-col>
    <el-col :span="11" :offset="1">
      <div class="words">
        <el-input type="textarea" :autosize="{minRows:4,maxRows:6}" placeholder="欢迎您了解..." v-model="textarea">
        </el-input>
      </div>
      
      <div class="area inputs">
      <span class="input-title">地区</span>
      <div class="input-content">
        <el-select v-model="value" placeholder="省/市">
         <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
         </el-option>
       </el-select>
       <el-select v-model="value1" placeholder="地/州">
         <el-option v-for="item in options1" :key="item.value" :label="item.label" :value="item.value">
         </el-option>
       </el-select>
       <el-select v-model="value2" placeholder="区/县">
         <el-option v-for="item in options2" :key="item.value" :label="item.label" :value="item.value">
         </el-option>
       </el-select>
      </div>
      </div>
      <div class="name inputs">   
      <span class="input-title">姓 名</span>
      <div class="input-content">
      	<el-input placeholder="请输入姓名" v-model="name">
      	</el-input>
      </div>	
      </div>
      <div class="sex inputs">
  		<span class="input-title">性别</span>
  		<div class="input-content">
          <el-radio :label="1" v-model='sex'>男</el-radio>
          <el-radio :label="2" v-model='sex'>女</el-radio>
  		</div> 
      </div>
      <div class="telephone inputs">
        <span class="input-title">手 机</span>
        <div class="input-content">
          <el-input placeholder="请输入手机号码" v-model="tel">
      	</el-input>
      	<el-button type="primary">获取验证码</el-button>
        </div>
      </div>
      <div class="code inputs">
        <span class="input-title">验证码</span>
        <div class="input-content">
          <el-input placeholder="请输入验证码" v-model="code">
      	</el-input>
      	
        </div>	
      </div>
      <div class="submit">
       <el-button type="primary">提交</el-button>
      </div>
    </el-col>
  </el-row>
  
</div>

</template>
<script>
  export default{
    name:'Contact',
    data(){
      return {
        textarea:'',
        options:[
          {value:'choice1',label:'北京'},
          {value:'choice2',label:'上海'},
          {value:'choice3',label:'广州'},
          {value:'choice4',label:'深圳'},
          {value:'choice5',label:'成都'},
          {value:'choice6',label:'杭州'},
        ],
        options1:[
          {value:'choice11',label:'北京'},
          {value:'choice12',label:'上海'},
          {value:'choice13',label:'广州'},
          {value:'choice14',label:'深圳'},
          {value:'choice15',label:'成都'},
          {value:'choice16',label:'杭州'},
        ],
        options2:[
          {value:'choice21',label:'北京'},
          {value:'choice22',label:'上海'},
          {value:'choice23',label:'广州'},
          {value:'choice24',label:'深圳'},
          {value:'choice25',label:'成都'},
          {value:'choice26',label:'杭州'},
        ],
        value:'',
        value1:'',
        value2:'',
        name:'',
        sex:'1',
        tel:'',
        code:'',
        words:[
          {no:1,content:'项目很好，请尽快联系我详谈。'},
          {no:2,content:'您好，请问我所在地区有代理商了吗？'},
          {no:3,content:'留下邮箱了，请将详细资料邮件给我。'},
          {no:4,content:'想了解流程，请找我联系。'},
          {no:5,content:'您好，作为代理商能得到哪些支持？'},
          {no:6,content:'请问投资所需要的费用有哪些？'},
          {no:7,content:'请电话联系我。'},
          {no:8,content:'有什么优惠吗？'},
          {no:9,content:'如果加盟，大概的多长时间会有所收益？'},
          {no:10,content:'项目很好，请尽快联系我详谈。'},
          {no:11,content:'项目很好，请尽快联系我详谈。'},
          {no:12,content:'如果加盟，大概的多长时间会有所收益？'},
          {no:13,content:'项目很好，请尽快联系我详谈。'}
        ]
      }
    },
    methods:{
      changeTextarea(item){
        this.textarea=item.content;
      }
    }
  }
</script>
<style>
	.text{
	  font-size:14px;
	}
  .text:hover{
    color:#409eff;
    cursor:pointer;
  }
	.item{
	  margin-bottom:4px;
	  text-align:left;
	}
	.clearfix:before,.clearfix:after{
	  display:table;
	  content:'';
	}
	.clearfix:after{
	  clear:both;
	}
	.words{
	  width:84%;
	}
	.area .el-select{
	  width:100px;
	}
	.inputs{
	  diaplay:inline-block;
	  text-align:left;
	  margin:15px 0px;
	}
	.telephone .el-input,.name .el-input,.code .el-input{
	  width:52%;
	}
	.input-title{
	  display:inline-block;
	  width:70px;
	  text-align:center;
	}
	.input-content{
	  display:inline;
	}
</style>


