<template>
  <div id="app">
    <el-header>
     <div class="header-navs">
        <div class="header-logo">
          <img src="./assets/logo.png">
        </div>
        <div class="nav-content">
          <el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" background-color="#409eff" text-color="#fff" active-text-color="red" router>
            <el-menu-item index="one"><a href="#">首页</a></el-menu-item>
            <el-menu-item index="project"><a href="#">项目介绍</a></el-menu-item>
            <el-menu-item index="3"><a href="#">加盟规则</a></el-menu-item>
            <el-menu-item index="question"><a href="#">加盟问答</a></el-menu-item>
            <el-menu-item index="5"><a href="#">联系我们</a></el-menu-item>
             <el-menu-item index=""><a href="#"><i class="el-icon-phone-outline"></i>400-003-1202</a></el-menu-item>
          </el-menu>
        </div>
     </div>
    </el-header>
    <div style="width:100%">
      <router-view/>
    </div>
    <div class="bottom">
      <Footer></Footer>
    </div>
    <div class="top" @click="backTop()">
      <a href="#">
        <el-button type="success" icon="el-icon-caret-top" circle></el-button>
      </a>    
    </div>
  </div>
</template>

<script>
import $ from 'jquery'
import Nav from '@/components/funccom/Nav'
import Footer from '@/components/funccom/Footer'
export default {
  name: 'App',
  components:{
   Nav,Footer
  },
  computed:{
   activeIndex(){
      return this.$route.path.replace('/','')
    }
  },
  methods:{
    backTop:function(){
      $('html,body').animate({scrollTop:0},700);
    }
  }
}
</script>

<style>
body{
  padding:0;
  margin:0;
}
#app {
  font-family: "Helvetica Neue",Helvetica,"PingFang SC","Hiragino Sans GB","Microsoft YaHei","微软雅黑",Arial,sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin: 0px;
  padding:0px;
}
.top{
  position:fixed;
  bottom:40px;
  right:20px;
}
.el-header{
  background-color:rgb(64, 158, 255);
  }
.header-logo{
    float:left;
    margin-left:10px;
  }
  .header-logo img{
    height:60px;
  }
  .nav-content{
    float:right;
  }
  .nav-content a{
    text-decoration:none;
  }
  .bottom{
    padding:50px 0px;
    padding-bottom:0px;
  }
 .nav-content .el-menu-item i{
   color:white;
 }
</style>



