         <h3>留言板</h3>
         <Tableform ref="childData" :message="ques"></Tableform>
        </div>
      </el-col>
    </el-row>
  </section>
    
    <section class="comment">
      <div class="comment-section">
      <img src="../assets/girls.png">
        <h3>以往留言</h3>
          <div class="question-answer1">
            <ul class="question-ul">
             <li v-for="item in ques" :key="item.no">
              <div><strong class="name">{{item.name}}</strong><strong class="problem">{{item.problem}}</strong><span class="area">{{item.area}}</span><span class="time">{{item.time}}</span></div>
             </li>
            </ul>
          </div> 
      </div>
    </section>
  </div>
</template>
<script>
import $ from 'jquery'
import Tableform from '@/components/funccom/Tableform'

  export default{
    name:'Question',
    components:{
      Tableform
    },
    data(){
      return{
        ani:null,
        ques:[
          {no:1,name:'张先生1',problem:'请问在二线城市需要投资多少钱？',area:'北京',time:'2017-01-02'},
          {no:2,name:'张先生2',problem:'具体需要选择多大面积的店面？最小面积是多少？',area:'北京',time:'2017-01-02'},
          {no:3,name:'张先生3',problem:'二线城市的机器人教育能发展起来么？是否有专人给我们分析发展的可能性？',area:'北京',time:'2017-01-02'},
          {no:4,name:'张先生4',problem:'贵公司提供的人员会单收费用么？',area:'北京',time:'2017-01-02'},
          {no:5,name:'张先生5',problem:'请问在二线城市需要投资多少钱？',area:'北京',time:'2017-01-02'},
          {no:6,name:'张先生6',problem:'请问在二线城市需要投资多少钱？',area:'上海',time:'2017-01-02'},
          {no:7,name:'张先生7',problem:'请问在二线城市需要投资多少钱？',area:'天津',time:'2017-01-02'},
          {no:8,name:'张先生8',problem:'请问在二线城市需要投资多少钱？',area:'河南',time:'2017-01-02'},
