        ]
      }
    },
    mounted:function(){
      this.$nextTick(function(){
      this.hover();
      })
    },
    methods:{
      scroll(){
        var ul=$(".question-ul:eq(0)");
        var li=ul.children();
        var liheight=$(li[0]).height();
        var upheight=0-13*liheight;
        $('.question-ul').animate({"marginTop":upheight},40000,function(){
          ul.css({marginTop:0})
        })
        
      },
      autoplay(){
        this.ani=window.setInterval(this.scroll,2000);
      },
      autostop(){
        window.clearInterval(this.ani);
      },
      hover(){
        
        $('.question-ul').hover(this.autostop,this.autoplay).mouseout();
      }
    },

  }
</script>
<style>
.nowcomment{
  padding:80px 0px;
}
.comment{
  background-image:url(../assets/bg5.jpg);
  padding:50px 0px;
}
.comment-section{
  width:80%;
  margin:0 auto;
}
.question-answer1{
  height:343px;
  border:1px dotted transparent;
  overflow:hidden;
}
.question-ul{
 margin-top:0px;
}
.question-ul li{
  height:48px;
  line-height:48px;
  list-style:none;
  border-bottom:1px solid rgb(64, 158, 255);
}
.question-ul li div{
   text-align:left;
   text-indent:25px;
}
.name,.problem,.area,.time{
  padding-right:50px;
}
.name{
  width:50px;
}

</style>


