<template>
  <el-carousel height="400px" indicator-position="none">
    <el-carousel-item v-for="item in banner" :key="item.id">
    	<img :src="item.img"/>
    	<div class="intro">
    	  <h3>{{item.intro}}</h3>
    	</div>
    </el-carousel-item>
  </el-carousel>
</template>

<script>
  export default{
    name:'Carousel',
    data(){
      return {
        banner:[
          {id:1,img:require('../../assets/banner2.jpg'),intro:'欢迎加入我们'},
          {id:2,img:require('../../assets/banner3.jpg'),intro:'欢迎加入我们'},
          {id:3,img:require('../../assets/banner4.jpg'),intro:'欢迎加入我们'},
        ]
      }
    }
  }
</script>
<style>
.el-carousel__item .intro{
	font-size:18px;
	opacity:0.75;
	line-height:400px;
	margin:0;
	margin-top:-700px;
}
</style>


