<template>
   <div>
   	<el-container class="hea">
	  <el-main>
	    <div class="banner">
	      <el-row>
	        <el-col :span="24">
	           <Carousel></Carousel>
	        </el-col>
	      </el-row>     
	    </div>
	    <div class="product" id="itemintro">
	      <div class="product-intro">
	        <h3>公司项目介绍</h3>
	        <el-row :gutter="30">
	          <el-col :span="6" v-for="(o,index) in cards" :key="index">
	            <el-card :body-style="{padding:'0px'}">
	              <img class="product-img" :src="o.img">
	              <div class="word-intro">
	                <span>{{o.title}}</span>
	                <div class="bottom clearfix">
	                  <p>{{o.intro}}</p>
	                  <el-button type="text" class="button">查看详细</el-button>
	                </div>
	              </div>
	            </el-card>
	          </el-col>
	        </el-row>
	      </div>
	    </div>
	    <div class="services">
	      <div class="services-intro">
	        <h3>STEAM教育</h3>
	        <el-row v-for="ser in services" :key="ser.id">
	          <el-col :span="8" v-for="item in ser.detail" :key="item.id">
	            <div class="services-introo" >
	              <img src="../assets/services.jpg"/>
	              <h4>{{item.title}}</h4>
	              <p>{{item.intro}}</p>
	            </div>
	          </el-col>
	        </el-row>
	      </div>
	    </div>
	    <div class="join" id="join">
	      <div class="join-intro">
	        <h3>加盟流程</h3>
	        <div class="join-content">
	        <el-row>
	          <el-col :span="12">
	          	<div class="join-img">
	          	  <ul class="join-banner">
	          	    <li v-for="item in joinimgs"><img :src="item.url" :key="item.id"/></li>
	          	  </ul>
	          	</div>
	          </el-col>
	          <el-col :span="12">
	            <div class="join-steps">
	              <el-steps direction="vertical" :active="active" finish-status="finish">
	                <el-step :title="item.title" :description="item.desc" v-for="(item,index) in stepss" :key="item.title" @click="changeImg(index)"></el-step>
	             
	              </el-steps>
	            </div>   
	          </el-col>
	        </el-row> 
	        </div>
	      </div>
	    </div>
	    <div class="contact" id="contact">
	      <div class="contact-intro">
	        <h3>联系我们</h3>
	        <div class="contact-form">
	          <Contact></Contact>
	        </div>
	      </div>
	    </div>   
	  </el-main>
	</el-container>
   </div>

</template>

<script>
import $ from 'jquery'
import Carousel from '@/components/funccom/Carousel'
import Contact from '@/components/funccom/Contact'
export default{
	name:'One',
	components:{
	  Carousel,Contact
	},
	data(){
	  return {
	    cards:[
	      {id:1,title:"师资团队",img:require('../assets/intro11.jpg'),intro:"公司由深谙教育理念、经验丰富、具有上市公司丰富管理经验高管团队成立。"},
	      {id:2,title:"课程体系",img:require('../assets/intro22.jpg'),intro:"以机器人教育、创客、编程、科学、心理教育为主线，形成了理念前沿、结构成熟、应用完整的课程体系。"},
	      {id:3,title:"管理职能",img:require('../assets/intro33.jpg'),intro:"拥有独立、完善的教研、教学、运营体系，承载着全国各地培训中心课程输送、教学指导等职能。"},
	      {id:4,title:"教育理念",img:require('../assets/intro44.jpg'),intro:"以儿童兴趣为出发点，结合实操动手。在玩中学、学中乐，乐中探。"}
	    ],
	    services:[
	      {
	      	id:'1',
	        detail:[
	          {id:'11',title:'跨学科',intro:'理工科教育出现了取消分科、进行整合教育的趋势。STEM教育因此应运而生，跨学科性是它最重要的核心特征。'},
	          {id:'12',title:'趣味性',intro:'把多学科知识融于有趣、具有挑战性、与学生生活相关的问题中，问题和活动的设计能激发学习者内在的学习动机。'},
	          {id:'13',title:'体验性',intro:'STEAM教育更强调学生动手、动脑，参与学习过程。这种在参与、体验中学习知识的方式对学生今后的工作和生活的长远发展会产生深刻影响。'}
	        ]
	      },
	      {
	      	id:'2',
	        detail:[
	          {id:'21',title:'情境性',intro:'理工科教育出现了取消分科、进行整合教育的趋势。STEM教育因此应运而生，跨学科性是它最重要的核心特征。'},
	          {id:'22',title:'协作性',intro:'把多学科知识融于有趣、具有挑战性、与学生生活相关的问题中，问题和活动的设计能激发学习者内在的学习动机。'},
	          {id:'23',title:'设计性',intro:'STEAM教育更强调学生动手、动脑，参与学习过程。这种在参与、体验中学习知识的方式对学生今后的工作和生活的长远发展会产生深刻影响。'}
	        ]
	      }  
	    ],
	    
	    stepss:[
 		  {title:'1',status:'wait',desc:'电话咨询六一机器人教育企业方'},
 		  {title:'2',status:'wait',desc:'达成意向后前往六一机器人教育公司实地考察'},
 		  {title:'3',status:'wait',desc:'寻找适合六一机器人教育加盟店铺'},
 		  {title:'4',status:'wait',desc:'签署六一机器人教育加盟合作意向书'},
 		  {title:'5',status:'wait',desc:'交纳加盟费用'},
 		  {title:'6',status:'wait',desc:'六一机器人教育公司择日前往考察'},
 		  {title:'7',status:'wait',desc:'加盟商签订卖场租约'},
 		  {title:'8',status:'wait',desc:'公司根据店铺情况设计装修图与陈列图'},
 		  {title:'9',status:'wait',desc:'加盟商进行店铺装修公司协助策划、培训、配货、宣传等开业筹备'},
 		  {title:'10',status:'wait',desc:'开店并反馈网站客服开店成功'},
 		  {title:'11',status:'wait',desc:'经营并成为老板，开店盈利'},
	    ],
	    joinimgs:[
	     {id:1,url:require("../assets/img3.jpg")},
	     {id:2,url:require("../assets/join1.jpg")},
	     {id:3,url:require("../assets/join2.jpg")},
	     {id:4,url:require("../assets/join3.jpg")},
	     {id:5,url:require("../assets/join4.jpg")},
	     {id:6,url:require("../assets/join5.jpg")},
	     {id:7,url:require("../assets/join1.jpg")},
	     {id:8,url:require("../assets/join2.jpg")},
	     {id:9,url:require("../assets/join3.jpg")},
	     {id:10,url:require("../assets/join4.jpg")},
	     {id:11,url:require("../assets/join5.jpg")}
	    ],
	    active:0
	  }
	},
	created:function(){
	},
	computed:{
	},
	mounted:function(){
	  this.$nextTick(function(){
	  	this.bind();
	  })
	},
	methods:{
	  bind(){
	  	var that=this;
	   $(".el-step").click(function(){
	   	 var index=parseInt(this.lastChild.firstChild.innerText)
	     
	     that.active=index;
	     $('.join-banner').animate({bottom:((index-1)*527)+'px'},'fast','swing',function(){}) 
	   });
	   var i=0;
	   var timer=setInterval(function(){
	   if(i<11){
	   $('.join-banner').animate({bottom:(i*527)+'px'})
	   	 i++;
	   	 that.active=i;
	   }
	   },4000)
	  }
	}
}
</script>

<style>
.el-container{
	width:100%;
}
  .el-main {
    background-color: #fff;
    color: #333;
    padding:0px;
  }
  .product,.services,.join,.contact{
    width:95%;
    margin:0 auto;
    padding:40px 0px;
  }
  #contact{
   margin-bottom:40px;
  }
  .product{
  	margin-top:25px;
  }
  .product-intro h3,.services-intro h3,.contact-intro h3,.join-intro h3{
  	width:250px;
  	height:50px;
  	border-bottom:1px solid #999;
  	text-align:center;
  	margin:30px auto;
  }
  .product-img{
    width:85%;
  }
  @keyframes rotate{
    0%{transform:rotate(0deg) scale(0.9);}
    100%{transform:rotate(-360deg) scale(1);}
  }
  .product-img:hover{
    cursor:pointer;
    animation:rotate .5s linear;
  }
  .bottom p{
    font-size:12px;
    color:#999;
  }
  .services{
    margin-top:20px;
  }
  .services-introo{
    padding:20px;
  }
  .services-introo:hover{
    border:1px double #ccc;
  }
  .services-intro p{
    font-size:12px;
    color:#999;
  }
  .join-img{
    overflow:hidden;
    position:relative;    
  }
  .join-img ul{
    width:392px;
    height:527px;
    position:relative;
    margin:0 auto;
    padding:0;
  }
  .join-img ul li{
     width:392px;
     height:527px;
     list-style:none;
  }
  .join .el-step__icon:hover{
    cursor:pointer;
    transform:scale(1.1);
    color:green;
    border-color:green;
  }
  
</style>


