<template>
  <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="que-ruelForm">
    <el-form-item label="留言" prop="content">
      <el-input type="textarea" v-model="ruleForm.content"></el-input>
    </el-form-item>
    <el-form-item label="姓名" prop="name">
      <el-input v-model="ruleForm.name"></el-input>
    </el-form-item>
    <el-form-item label="性别" prop="sex">
      <el-radio-group v-model="ruleForm.sex">
        <el-radio label="男"></el-radio>
        <el-radio label="女"></el-radio>
      </el-radio-group>
    </el-form-item>
    <el-form-item label="电话" prop="tel">
      <el-input v-model="ruleForm.tel"></el-input>
    </el-form-item>
    <el-form-item label="地区" required>
      <el-col :span="11">
        <el-form-item prop="city">
          <el-select v-model="ruleForm.city">
            <el-option label="北京" value="shanghai"></el-option>
            <el-option label="上海" value="beijing"></el-option>
          </el-select>
        </el-form-item>
      </el-col>
    </el-form-item>
    <el-form-item>
      <el-button type="primary" @click="submitForm('ruleForm')">立即留言</el-button>
      <el-button @click="resetFrom('ruleForm')">重置</el-button>
    </el-form-item>
  </el-form>  
</template>

<script>
export default{
	props:['message'],
	name:'Tableform',
	data(){
	  return {
	    ruleForm:{
	      content:'',
	      name:'',
	      sex:'',
	      tel:'',
	      city:''
	    },
	    rules:{
	      content:[
	        {required:true,message:'期待您的留言',trigger:'blur'}
	      ],
	      name:[
	        {required:true,message:'请输入姓名',trigger:'blur'},
	        {min:2,max:5,message:'长度在2到5个字符',trigger:'blur'}
	      ],
	      sex:[
	        {required:true,message:'请选择性别',trigger:'change'},
	      ],
	      tel:[
	        {required:true,message:'请输入手机号码',trigger:'blur'},
	      ],
	      city:[
	        {required:true,message:'请输入地区',trigger:'change'}
	      ]
	    }
	  }
	},
	methods:{
	  submitForm(formName){
	    this.$refs[formName].validate((valid)=>{
	      if(valid){
	        console.log(message);
	        var no=message.length++;
	        message.push({
	        	'no':no,
	        	'name':this.ruleForm.name,
	        	'problem':this.ruleForm.content,
	        	'area':this.ruleForm.city
	        })
	        alert('submit');
	      }else{
	        console.log('error');
	        return false;
	      }
	    })
	  },
	  resetForm(formName){
	    this.$refs[formName].resetFields();
	  }
	}
}
</script>

<style>

</style>


