	      {id:2,text:'隐私条款'},
	      {id:3,text:'京ICP备12345678号'},
	      {id:4,text:'法律声明'},
	      {id:5,text:'京公网安备12684320909098号'}
	    ]
	  }
	}
}
</script>

<style>
  .el-footer{
     background-color:#303133;
     color: #fff;
  }
  .company-intro{
    margin:auto;
  }
  .company-ul{
  	  width:800px;
     font-size:12px;
     margin:auto;
  }
  .company-ul li{
    list-style:none;
    float:left;
    padding:15px 8px;
  }
</style>


